<template>
    <div>
        <h1>Trade or view your portfolio</h1>
        <h6>You may save & load your Data</h6>
        <h6>Click on End day to begin a New Day!</h6>
        <hr>
        <p>Your funds is: <strong>{{ funds | toCurrency}}</strong></p>
    </div>
</template>

<script>
export default {
    data() {
        return {
            funds: this.$store.getters.funds
        }
    }
}
</script>




