<template>
    <div class="col-md-4 mb-4">
        <div class="card">
            <div class="card-header">
                <h5 class="card-title mb-0">{{stock.name}}
                    <small>(Price: {{ stock.price | toCurrency }})</small>
                </h5>
            </div>
            <div class="card-body">
                <div class="input-group">
                    <input 
                        type="number" 
                        min="0"
                        class="form-control"
                        v-model.number="quantity">
                    <button 
                        href="#" 
                        class="btn btn-success"
                        @click="buyStock"
                        :disabled="btnValid">{{insufficientFunds ? 'Insufficient Funds' : 'Buy' }}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['stock'],
    data(){
        return {
            quantity: 0
        }
    },
    computed: {
        funds() {
            return this.$store.getters.funds;
        },
        insufficientFunds(){
            return this.quantity * this.stock.price > this.funds;
        },
        btnValid(){
            return this.insufficientFunds || this.quantity <= 0 || !Number.isInteger(this.quantity);
        }
    },
    methods: {
        buyStock(){
            const order = {
                stockId: this.stock.id,
                stockPrice: this.stock.price,
                quantity: this.quantity
            }
            this.$store.dispatch('buyStock', order);
            this.quantity = 0;
        }
    }
}
</script>



