<template>
    <div class="col-md-4 mb-4">
        <div class="card">
            <div class="card-header">
                <h5 class="card-title mb-0">{{stock.name}}
                    <small>(Price: {{ stock.price | toCurrency }} | Quantity: {{ stock.quantity }})</small>
                </h5>
            </div>
            <div class="card-body">
                <div class="input-group">
                    <!-- you can just set max value like this - :max="stock.quantity"  -->
                    <input 
                        type="number"
                        min="0"
                        class="form-control"
                        v-model.number="quantity">
                    <button 
                        href="#" 
                        class="btn btn-success"
                        @click="sellStock"
                        :disabled="btnValid">{{overSell ? 'Not enougth stocks' : 'Sell'}}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props: ['stock'],
    data(){
        return {
            quantity: 0
        }
    },
    computed: {
        overSell() {
            return this.quantity > this.stock.quantity;
        },
        btnValid() {
            return this.overSell || this.quantity <= 0 || !Number.isInteger(this.quantity);
        }
    },
    methods: {
        sellStock() {
            const order = {
                stockId: this.stock.id,
                stockPrice: this.stock.price,
                quantity: this.quantity
            }
            this.$store.dispatch('sellStock', order);
            this.quantity = 0;
        }
    }
}
</script>



